import React, { useState } from 'react';
import { Button, FormGroup, InputLabel, Stack, TextField } from '@mui/material';
import validator from 'validator';

interface Props {
    setUrl: (value: string) => void;
}

export default function UrlForm(props: Props) {
    const { setUrl } = props;
    const [url, setEnteredUrl] = useState('');
    const [error, setError] = useState('');

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        // Validate URL
        if (!validator.isURL(url)) {
            setError('Please enter a valid URL.');
            return;
        }

        // Normalize URL: Add 'https://' if not provided
        let normalizedUrl = url;
        if (!url.startsWith('http://') && !url.startsWith('https://')) {
            normalizedUrl = 'https://' + url;
        }

        // Clear error if URL is valid
        setError('');

        // Set URL in parent component
        setUrl(normalizedUrl);
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setEnteredUrl(e.target.value);
    };

    return (
        <form onSubmit={handleSubmit} autoComplete="off">
            <Stack spacing={2}>
                <FormGroup>
                    <InputLabel>URL</InputLabel>
                    <TextField
                        name="url"
                        size="small"
                        placeholder="Enter your URL here..."
                        value={url}
                        onChange={handleInputChange}
                        error={!!error}
                        helperText={error || 'https://example.com/any-path'}
                        required
                    />
                </FormGroup>
                <Button variant="contained" type="submit" disableElevation>
                    Generate
                </Button>
            </Stack>
        </form>
    );
}
